#photo-container {
  display: flex;         
  flex-direction: column; 
  align-items: flex-start;   
  margin-bottom: 20px;   
}

#photoLviv {
  margin-top: 20px; 
}

button {
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
  border: none;
  border-radius: 5px; 
  font-size: 16px;
  transition: background-color 0.3s ease, transform 0.2s ease; 
}

#btnAdd {
  background-color: #28a745; 
  color: white;
}

#btnIncrease {
  background-color: #007bff; 
  color: white;
}

#btnReduce {
  background-color: #ffc107; 
  color: black;
}

#btnDelete {
  background-color: #dc3545; 
  color: white;
}

button:hover {
  transform: translateY(-2px); 
}

button:active {
  transform: translateY(0); 
  opacity: 0.8; 
}